import React from "react";
import { BrowserRouter, Route, Link, Switch } from "react-router-dom";
import Footer from "./components/Footer";
import HomeScreen from "./screens/HomeScreen";
import TeamDetail from "./screens/TeamDetail";

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <header>
          <Link to={process.env.PUBLIC_URL + "/page/1"}>Home</Link>
        </header>
        <main>
          <Switch>
            <Route path={process.env.PUBLIC_URL + "/"} component={HomeScreen} exact />
            <Route path={process.env.PUBLIC_URL + "/page/:page"} component={HomeScreen} exact />
            <Route path={process.env.PUBLIC_URL + "/teamdetail/:team"} component={TeamDetail} />
          </Switch>
        </main>
        <footer>
          <Footer />
        </footer>
      </div>
    </BrowserRouter>
  );
}

export default App;
